img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

h1 {
  font-size: 2.2rem;
  font-weight: 400;
  margin: 20px 0;
}

@media (max-width: 500px) {
  img {
    margin: 0;
    padding: 0;
    border-radius: 0;
  }

  h1 {
    padding-left: 1em;
  }
  p {
    padding: 0 2em;
  }
}
